<template>
	<div v-if="isOnProfile" class="form-section">
		<BrainstormResults v-if="showResults" :results="responseData" :profileInfo="profileInfoData" />
		<div class="headline">
			<i class="pi pi-info"></i>
			<h4>Your business details</h4>
		</div>
		<span class="p-input">
			<TextArea v-model="messageSettings.details" maxlength="250" :autoResize="true" rows="4" cols="30"
				placeholder="Briefly describe what you are offering." />
			<div class="indicator-wrapper">
				<div class="textarea-length-indicator">
					{{ messageSettings.details.length }}/250
				</div>
			</div>
		</span>
		<span>
			<ButtonMain class="save-button settings-button" :label="'Analyze ' + profileInfoData.name + '\'s Profile'"
				@click="analyzeProfile" :loading="isAnalyzeLoading" />
		</span>
	</div>
	<div v-else>
		<div class="col-12 md:col-4 mb-12 px-5">
			<div class="p-link p-3 shadow-2 mb-3 inline-block" style="border-radius: 10px">
				<i class="pi pi-linkedin text-8xl bs-icon"></i>
			</div>
			<div class="text-slate-700 mb-3 font-medium">Linked Profile Analysis</div>
			<div class="text-lg mb-3 font-medium">No profile detected</div>
			<span class="font-normal text-gray-500">
				Visit a LinkedIn profile to use Brainstorm.
			</span>
		</div>
	</div>
</template>

<script>
import { onMounted, reactive, ref } from "vue";
import { useAppState, syncLocalStorage } from "@/snippets/system";
import { getAnalysis } from "@/snippets/api";
import BrainstormResults from "./BrainstormResults";

export default {
	components: {
		BrainstormResults,
	},
	setup() {
		const appState = useAppState();
		const presetName = ref("");
		const id = ref("");
		const isOnProfile = ref(false);
		const messageSettings = reactive({
			details: "",
		});

		let profileInfoData = reactive({ name: "" });
		const isAnalyzeLoading = ref(false);
		let responseData = reactive({
			summary: "",
			services: "",
			questions: "",
		});

		const showResults = ref(false);

		onMounted(() => {
			//wait for local storage to finish loading before setting values
			if ("brainstorm" in appState.activeConfigs) {
				presetName.value = appState.activeConfigs["brainstorm"]["presetName"];
				id.value = appState.activeConfigs["brainstorm"]["id"];
				messageSettings.details =
					appState.activeConfigs["brainstorm"]["messageSettings"]["details"];
			}
			// get current url from tab
			//if in chrome extension environment
			if (chrome.tabs) {
				chrome.tabs.query(
					{ active: true, currentWindow: true },
					function (tabs) {
						var activeTab = tabs[0];
						var url = activeTab.url;

						//if url is linkedin profile set isOnProfile to True
						if (url.includes("linkedin.com/in/")) {
							isOnProfile.value = true;
							//get profile info from tab
							chrome.tabs.sendMessage(
								activeTab.id,
								{ action: "getProfileInfo" },
								function (response) {
									console.log(response);
									profileInfoData.name = response.name;
									profileInfoData.headline = response.headline;
									profileInfoData.about = response.about;
								}
							);
						}
					}
				);
			}
		});

		const analyzeProfile = async () => {
			//save preset name and id
			isAnalyzeLoading.value = true;
			appState.activeConfigs["brainstorm"] = {
				presetName: presetName.value,
				id: id.value,
				messageSettings: messageSettings,
			};
			await syncLocalStorage("activeConfigs", appState.activeConfigs);
			const response = await getAnalysis(
				profileInfoData,
				messageSettings.details
			);
			if (response.services) {
				//console.log(response);
				responseData.services = response.services;
				responseData.questions = response.questions;
				responseData.summary = response.summary;
				responseData.approach = response.approach;
				showResults.value = true;
			}
			isAnalyzeLoading.value = false;
		};

		return {
			messageSettings,
			showResults,
			isOnProfile,
			profileInfoData,
			responseData,
			isAnalyzeLoading,
			analyzeProfile,
		};
	},
};
</script>

<style></style>