<template>
	<div v-if="appState.loggedIn">
		<div class="content-header">
			<div class="credits" @click="openInNewTab(`https://${appDomain}/product/credits`)">
				<i class="pi pi-envelope"></i>Credits: {{ appState.user.credits }}
			</div>
			<!-- <div class="settings"><i class="pi pi-cog"></i>Settings</div> -->
			<div class="help">
				<i class="pi pi-question" @click="
					openInNewTab(
						`https://go.crisp.chat/chat/embed/?website_id=${crispId}&user_email=${email}&user_nickname=${name}`)
				"></i>
			</div>
			<img class="logo" alt="Logo" :src="logoUrl" />
			<div class="spacer"></div>
			<div class="tabs">
				<div class="tab-header">
					<div class="active"><i class="fa fa-paper-plane"></i> Messaging</div>
					<div><i class="fa fa-brain"></i> Brainstorm</div>
					<div><i class="fa fa-cog"></i> Settings</div>
				</div>
				<div class="tab-indicator"></div>
			</div>
		</div>
		<div class="tab-body content-body">
			<div class="general-section tab-content active pt-10">
				<p>
					<GeneralSection />
				</p>
			</div>
			<div class="brainstorm-section tab-content pt-10">
				<BrainstormSection />
			</div>
			<div class="settings-section tab-content pt-10">
				<SettingsSection />
			</div>
		</div>
		<div class="content-footer"></div>
	</div>
	<div v-else>
		<LoginPage />
	</div>
</template>

<script>
import { onMounted, ref, nextTick } from "@vue/runtime-core";
import { initTabs } from "./snippets/tabfunctions";
import GeneralSection from "./components/forms/GeneralSection.vue";
import SettingsSection from "./components/forms/SettingsSection.vue";
import BrainstormSection from "./components/forms/BrainstormSection.vue";
import LoginPage from "./components/support/LoginPage.vue";
import { useAppState, syncLocalStorage } from "./snippets/system";
import { getUser } from "./snippets/api";

export default {
	components: {
		GeneralSection,
		LoginPage,
		SettingsSection,
		BrainstormSection,
	},
	//composition api setups for reactive data
	setup() {
		//setup system, get user opject, sync with database local storage
		const appState = useAppState();
		const email = ref("");
		const name = ref("");
		const logoUrl = ref("/img/" + process.env.VUE_APP_LOGO_URL);
		const appDomain = ref(process.env.VUE_APP_DOMAIN);
		const crispId = ref(process.env.VUE_APP_CRISP_ID)
		//init(appState);

		onMounted(async () => {
			try {
				await nextTick();
				if (appState.loggedIn) {
					initTabs();
					appState.user = await getUser(appState.user.email, appState.user.token);
					await syncLocalStorage("user", appState.user);
					email.value = encodeURIComponent(appState.user.email);
					name.value = encodeURIComponent(appState.user.firstName + " " + appState.user.lastName);					
				}
			} catch (error) {
				console.error('Error during mounted hook:', error);
			}

			//setMode(appState.user.mode)
			return true;
		});

		const openInNewTab = (url) => {
			chrome.tabs.create({ url: url, selected: true, active: true });
		};

		return {
			appState,
			useAppState,
			openInNewTab,
			email,
			name,
			logoUrl,
			appDomain,
			crispId

		};
	},
};
</script>

