<template>
	<div class="select-mode">
		<transition name="slide">
			<div class="general-header" v-if="showGeneralHeader">
				<h1 class="font-bold mb-4 text-lg">LinkedIn Messaging</h1>
				<DropDown v-model="selectedMode" :options="dropdownOptions" optionLabel="label" placeholder="Select a mode"
					@change="onModeChange" />
			</div>
		</transition>
		<div class="toggle-tab" @click="toggleGeneralHeader" v-if="showGeneralHeader == false">
			<i class="fas fa-chevron-down text-white"></i>
		</div>
		<div class="info-wrapper">
			<div class="info-section bg-gray-100 p-4 rounded-md shadow-md mb-4" v-if="hoveredButton">
				<h5 class="font-bold mb-2">{{ hoveredButton.label }}</h5>
				<p class="text-gray-600 text-xs">{{ hoveredButton.description }}</p>
			</div>

			<div class="info-section bg-gray-100 p-4 rounded-md shadow-md" v-else>
				<h5 class="font-bold mb-2">How to send messages</h5>
				<p class="text-gray-600 text-xs mb-2">Follow these simple steps to use {{ appName }}:</p>
				<div class="step-container">
					<div class="step-content">
						<!-- Step content goes here -->
					</div>

					<div class="step-navigation mt-4 flex justify-between items-center">
						<button @click="previousStep" :disabled="currentStep === 1"
							class="px-2 py-1 rounded-md mr-2 text-xs bg-gray-400 h-7">
							<i class="fas fa-arrow-left text-white"></i>
						</button>
						<div class="step-info text-gray-600 text-xs flex-grow mx-2">
							<p v-if="currentStep === 1"><strong>Step 1:</strong> Visit the LinkedIn profile of the prospect
								you want to
								send a message. If you recently installed the extension refresh the page.</p>
							<p v-if="currentStep === 2"><strong>Step 2:</strong>Choose a messaging mode that best suits your
								prospecting efforts
								(ex. Connection request, Recruiter etc.)
							</p>
							<p v-if="currentStep === 3"><strong>Step 3:</strong> Enter the information requested in the
								messaging mode and apply settings.
								You can also choose a previously saved preset.</p>
							<p v-if="currentStep === 4"><strong>Step 4:</strong> Open the message window on the user's
								profile. This can either be an InMail,
								direct message or connection request.</p>
							<p v-if="currentStep === 5"><strong>Step 5:</strong> Click the "Generate" button to create a
								hyper-personalized message,
								review, edit and then send.</p>
							<p v-if="currentStep === 6"><strong>Important:</strong> If enabled, disable the "press enter to send" on the individual message box by pressing the three dots</p>
							<p v-if="currentStep === 7"><strong>Important:</strong> The more information you provide the AI
								the better your messages will be. Provide as many details as possible in your message
								settings</p>
								<p v-if="currentStep === 7"><strong>Important:</strong> Messages take 5-15 seconds to generate depending on your settings</p>
							<p v-if="currentStep === 8"><strong>Important:</strong> This extension works best on profiles that have
								a few paragraphs in their about section.</p>
						</div>
						<button @click="nextStep" :disabled="currentStep === 8"
							class="px-2 py-1 rounded-md text-xs bg-gray-400 h-7">
							<i class="fas fa-arrow-right text-white"></i>
						</button>
					</div>
				</div>
			</div>
		</div>
	</div>
	<template v-if="mode !== 'Selection'">
		<component :is="selectedComponent" />
	</template>
</template>
<script>
import { ref } from 'vue';
import BasicSection from './BasicSection.vue';
import PitchSection from './PitchSection.vue';
import StarterSection from './StarterSection.vue';
import ConnectionSection from './ConnectionSection.vue';
import RecruitSection from './RecruitSection.vue';
import CustomSection from './CustomSection.vue';
import TemplateSection from './TemplateSection.vue';

export default {
	name: 'GeneralSection',
	components: {
		BasicSection,
		PitchSection,
		StarterSection,
		ConnectionSection,
		RecruitSection,
		CustomSection,
		TemplateSection,
	},

	setup() {
		const mode = ref('Selection');
		const selectedMode = ref(null);
		const hoveredButton = ref(null);
		const appName = ref(process.env.VUE_APP_TITLE)
		const currentStep = ref(1);

		const componentMapping = {
			Basic: BasicSection,
			Pitch: PitchSection,
			Starter: StarterSection,
			Connection: ConnectionSection,
			Recruit: RecruitSection,
			Custom: CustomSection,
			Template: TemplateSection,
		};

		const selectedComponent = ref(null);

		const showGeneralHeader = ref(true);

		const toggleGeneralHeader = () => {
			showGeneralHeader.value = !showGeneralHeader.value;
		};

		const closeGeneralHeader = () => {
			showGeneralHeader.value = false;
		};


		const dropdownOptions = [
			{
				value: 'Template',
				label: 'Template Mode',
				description:
					"Paste your sample message as a guide and Outboundly will personalize the message to the recipient based on their LinkedIn profile.",
				icon: 'fa-sharp fa-solid fa-code',
			},
			{
				value: 'Basic',
				label: 'General Mode',
				description:
					"Open ended mode great at crafting engaging messages that appeal to a prospect's interests and background.",
				icon: 'fa-sharp fa-solid fa-comment',
			},
			{
				value: 'Pitch',
				label: 'Pitch Mode',
				description:
					"Creates targeted pitches based on a prospect's pain points and interests. Focuses on the value of your offering.",
				icon: 'fa-sharp fa-solid fa-bullhorn',
			},
			{
				value: 'Starter',
				label: 'Conversation Starter Mode',
				description:
					"Break the ice with three conversation starters that are tailored to the prospect's professional background and interests.",
				icon: 'fa-sharp fa-solid fa-comment-dots',
			},
			{
				value: 'Recruit',
				label: 'Recruiter Mode',
				description:
					"This mode helps find the best talent by crafting messages that speak to candidates' experiences and aspirations.",
				icon: 'fa-sharp fa-solid fa-user-tie',
			},
			{
				value: 'Custom',
				label: 'Custom Mode',
				description:
					"Lets users specify how to personalize message. It increases engagement and response rates by speaking directly to the prospect.",
				icon: 'fa-regular fa-solid fa-pencil-alt',
			},
			{
				value: 'Connection',
				label: 'Connection Request Mode',
				description:
					"Generates tailored LinkedIn connection requests to increase acceptance rates and foster meaningful conversations.",
				icon: 'fa-regular fa-solid fa-user-plus',
			},
		];

		const onModeChange = () => {
			mode.value = selectedMode.value.value;
			hoveredButton.value = selectedMode.value;
			selectedComponent.value = componentMapping[mode.value];
			closeGeneralHeader();
		};

		const nextStep = () => {
			if (currentStep.value < 7) {
				currentStep.value += 1;
			}
		};

		const previousStep = () => {
			if (currentStep.value > 1) {
				currentStep.value -= 1;
			}
		};

		return {
			mode, selectedMode, hoveredButton, dropdownOptions, onModeChange, selectedComponent, appName, currentStep, showGeneralHeader, toggleGeneralHeader, closeGeneralHeader, nextStep, previousStep
		};

	},
};
</script>
<style scoped>
.slide-enter-active,
.slide-leave-active {
	transition: all 0.5s;
}

.slide-enter,
.slide-leave-to {
	transform: translateY(-100%);
	opacity: 0;
}

.toggle-tab {
	cursor: pointer;
	display: flex;
	justify-content: center;
	align-items: center;
	background-color: #3b82f6;
	width: 50px;
	height: 20px;
	border-radius: 12px;
	margin: 0 auto;
}

.step-navigation button[disabled] {
	opacity: 0.5;
	cursor: not-allowed;
}

.step-container.relative {
	position: relative;
}

.step-navigation.absolute {
	position: absolute;
}

.p-dropdown {
	width: 14rem;
	margin-bottom: 0px;
}

.info-wrapper {
	display: flex;
	justify-content: center;
	align-items: center;
	margin-bottom: 15px;
}

.info-section {
	width: 90%;
	margin-top: 20px;
	padding: 20px;
	background-color: #efefef;
	border-radius: 12px;
	max-width: 500px;
}

.info-section h3 {
	font-size: 1.2em;
	font-weight: bold;
	margin-bottom: 8px;
}

.info-section p {
	line-height: 1.4;
	color: #333;
}

.fa-solid,
.fas {
	font-weight: 200;
}

.fa-regular,
.far {
	font-weight: 200;
}</style>