<template>
	<div class="form-section">
		<DropDown v-model="selectedPreset" :options="Object.values(dropdownOptionsValues)" optionLabel="name"
			placeholder="Select a saved preset" @change="selectPresetAction" :loading="isDropdownLoading" />
		<div class="headline">
			<i class="pi pi-info"></i>
			<h4>Conversation Starter</h4>
		</div>
		<div class="p-input">
			<TextArea v-model="messageSettings.solution" maxlength="200" :autoResize="true" rows="1" cols="2"
				placeholder="Write the topic of your conversation" />
			<div class="indicator-wrapper">
				<div class="textarea-length-indicator">
					{{ messageSettings.solution.length }}/200
				</div>
			</div>
		</div>
		<div class="p-input">
			<TextArea v-model="messageSettings.questionToAsk" maxlength="100" :autoResize="true" rows="1" cols="2"
				placeholder="Write a question to ask your recipient" />
			<div class="indicator-wrapper">
				<div class="textarea-length-indicator">
					{{ messageSettings.questionToAsk.length }}/100
				</div>
			</div>
		</div>
		<ActionBar :messageSettings="messageSettings" messageMode="general" messageType="starter"
			:trashButton="trashDisabled" :selectedPreset="selectedPreset" />
	</div>
</template>

<script>
import { onMounted, ref, reactive, computed, watch} from "vue";
import { updateMessageSettings } from "../../snippets/api";
import { useAppState } from "@/snippets/system";
import ActionBar from "../blocks/ActionBar.vue";
import { getPreset } from "@/snippets/api";

export default {
	components: { ActionBar },
	setup() {
		const appState = useAppState();

		const presetName = ref("");
		const isDropdownLoading = ref(false);
		const selectedPreset = ref();
		const messageSettings = reactive({
			questionToAsk: "",
			solution: "",
			presetName: "",
			id: "",
		});

		//computed value for if selectedPreset is empty object set to false
		const selectPresetAction = async () => {
			isDropdownLoading.value = true;
			const config = await getPreset(selectedPreset.value);
			if (config.messageSettings) {
				messageSettings.questionToAsk = config.messageSettings.questionToAsk;
				messageSettings.solution = config.messageSettings.solution;
				messageSettings.id = config.id;
				messageSettings.presetName = config.presetName;

				trashDisabled.value = false;
			}

			isDropdownLoading.value = false;
		};

		const trashDisabled = computed(() => {
			return selectedPreset.value === undefined ? true : false;
		});

		const dropdownOptions = ref([]);
		const dropdownOptionsValues = computed(() => {
			return dropdownOptions.value ? Object.values(dropdownOptions.value) : [];
		});

		const updateDropdownOptions = () => {
			dropdownOptions.value = appState["user"]['presets-general-starter'];
		};

		watch(
			() => appState["user"]['presets-general-starter'],
			() => {
				updateDropdownOptions();
			}
		);

		onMounted(() => {
			if ("general-starter" in appState.activeConfigs) {
				messageSettings.questionToAsk =
					appState.activeConfigs["general-starter"]["messageSettings"][
					"questionToAsk"
					] || "";
				messageSettings.solution =
					appState.activeConfigs["general-starter"]["messageSettings"][
					"solution"
					] || "";
				messageSettings.id = appState.activeConfigs["general-starter"]["id"];
			}
			updateDropdownOptions();
		});

		const saveInfo = async () => {
			let settings = {
				id: messageSettings.id,
				messageMode: "general",
				messageType: "starter",
				messageSettings: {
					starter: messageSettings.questionToAsk,
					aboutYourself: messageSettings.solution,
				},
			};

			await updateMessageSettings(settings);
		};

		return {
			trashDisabled,
			isDropdownLoading,
			appState,
			selectedPreset,
			presetName,
			messageSettings,
			dropdownOptions,
			dropdownOptionsValues,
			saveInfo,
			selectPresetAction,
		};
	},
};
</script>

